import { Injectable} from '@angular/core';
import { UserInfo  } from './userInfo';


@Injectable()
export class ShareService{
 public data:UserInfo;
  constructor() { }
  send_info(data:UserInfo){
    this.data = data;
  }
  get_info():UserInfo{
    return this.data;
  }
 
}


